<template>
    <div class="coupon-item" @click="couponClick({price: coupon.reduce_price, id: coupon.user_coupon_id})">
      <div :class="{'coupon-wrapper': true, 'color-gray': coupon.is_receive || coupon.state.text === '已使用', 'color-blue': index%2 !== 0 && !coupon.is_receive && coupon.state.text !== '已使用'}">
        <div class="coupon-type">满减券</div>
        <div class="left">
            <div class="price-box">
                <span>￥</span>
                <span class="price">{{ coupon.reduce_price }}</span>
            </div>
            <div class="text">满{{ coupon.min_price }}元可用</div>
        </div>
        <div class="split-line"></div>
        <div class="right">
            <div class="top">{{ coupon.name }}</div>
            <div class="bot">
                <div class="l" v-if="type==='coupon1'">领取{{ coupon.expire_day }}天内有效</div>
                <div class="l" v-if="type==='coupon2' || type==='coupon3'">{{ coupon.start_time + '~' + coupon.end_time}}</div>
                <div class="r">
                    <button class="btn" v-if="type==='coupon1'&&!coupon.is_receive" @click="receiveCoupon(coupon.coupon_id)">立即领取</button>
                    <div class="text" v-if="type==='coupon1'&&coupon.is_receive">已领取</div>
                    <div class="text" v-if="type==='coupon2'">{{ coupon.state.text }}</div>
                </div>
            </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  props: {
    coupon: Object,
    index: Number,
    type: String
  },
  methods: {
    receiveCoupon (id) {
      this.$emit('receiveCoupon', id)
    },
    couponClick (price) {
      this.$emit('couponClick', price)
    }
  }
}
</script>

<style lang="less" scoped>
.coupon-item{
    width: 100%;
    margin-bottom: 10px;
    font-family: PingFang-Medium,PingFangSC-Regular,Heiti,Heiti SC,DroidSans,DroidSansFallback,Microsoft YaHei,sans-serif;
    .coupon-wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: linear-gradient(-128deg,#ff6d6d,#ff3636);
        border-radius: 4px;
        color: #fff;
        height: 90px;
        position: relative;
        overflow: hidden;
        .coupon-type{
            position: absolute;
            top: 0;
            right: 0;
            z-index: 5;
            width: 64px;
            padding: 3px 0;
            background: #a771ff;
            font-size: 10px;
            text-align: center;
            color: #fff;
            transform: rotate(45deg);
            transform-origin: 32px 32px;
        }
        .left{
            height: 100%;
            flex: 0 0 32%;
            border-radius: 0 8px 8px 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .price-box{
                font-size: 15px;
                .price{
                    font-size: 26px;
                    font-weight: bold;
                }
            }
            .text{
                font-size: 11px;
            }
        }
        .split-line{
            height: 100%;
            position: relative;
            border-left: 2px solid #fff;
            margin: 0 5px 0 3px;
            background-color: #fff;
            &::before,&::after{
                content: "";
                position: absolute;
                left: -7px;
                background: #f7f7f7;
                width: 12px;
                height: 6px;
            }
            &::before{
                top: 0;
                border-radius: 0 0 8px 8px;
            }
            &::after{
                bottom: 0;
                border-radius: 8px 8px 0 0;
            }
        }
        .right{
            height: 100%;
            flex: 1;
            padding: 15px 10px;
            border-radius: 8px 0 0 8px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-between;
            .top{
               font-size: 16px;
            }
            .bot{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                .l{
                    font-size: 12px;
                }
                .r{
                    .btn{
                        height: 23px;
                        width: 61px;
                        line-height: 21px;
                        text-align: center;
                        border: 1px solid #fff;
                        border-radius: 15px;
                        background-color: transparent;
                        font-size: 12px;
                    }
                    .text{
                        font-size: 12px;
                        margin-left: -40px;
                    }
                }
            }
        }
    }
    .color-gray{
        background: linear-gradient(-113deg,#bdbdbd,#a2a1a2);
        .coupon-type{
            background-color: #9e9e9e;
        }
    }
    .color-blue{
        background: linear-gradient(-125deg,#57bdbf,#2f9de2);
    }
}
</style>
