import goTopIcon from '@/assets/images/mescroll-totop.png'
export default {
  data () {
    return {
      finished: false,
      goTopIcon,
      isShowGoTop: false
    }
  },
  methods: {
    pageScroll (e) {
      // 滑动条滚动时，距离顶部距离
      const scrollTop = e.target.scrollTop
      // 可视区高度
      const windowHeight = e.target.clientHeight
      // 滑动条总高度
      const scrollHeight = e.target.scrollHeight
      // 是否显示返回顶部按钮
      if (scrollTop >= windowHeight * 2) {
        this.isShowGoTop = true
      } else {
        this.isShowGoTop = false
      }
      // 滑动条到达底部的条件
      if (scrollTop + windowHeight === scrollHeight) {
        if (this.page < this.pageTotal) {
          this.page++
          this.getList()
        } else {
          this.finished = true
        }
      }
    },
    refresh () {
      this.page = 1
      this.list = []
      this.finished = false
      this.isShowGoTop = false
      if (this.$refs.listDom) {
        this.$refs.listDom.scrollTop = 0
      }
    },
    goTop () {
      let scrollTop = this.$refs.listDom.scrollTop
      if (scrollTop > 0) {
        const speed = 50
        const time = Math.floor(200 / (scrollTop / speed))
        const t = setInterval(() => {
          if (scrollTop > 0) {
            scrollTop -= speed
            this.$refs.listDom.scrollTop = scrollTop
            return
          }
          clearInterval(t)
        }, time)
      }
    }
  }
}
