<template>
    <div class="container">
        <nav-bar title="我的优惠券" :border="false"></nav-bar>
        <van-tabs v-model="active">
          <van-tab name="isUnused" title="未使用"></van-tab>
          <van-tab name="isUse" title="已使用"></van-tab>
          <van-tab name="isExpire" title="已过期"></van-tab>
        </van-tabs>
        <div class="list" v-if="list.length > 0">
          <coupon-item :coupon="item" :index="index" type="coupon2" v-for="(item, index) in list" :key="item.user_coupon_id"></coupon-item>
          <div class="end" v-if="finished">没有更多了</div>
        </div>
        <div class="empty" v-else>
          <van-empty description="暂无内容" />
        </div>
        <img :src="goTopIcon" alt="" :class="{'goTop': true, 'goTop-show': isShowGoTop}" @click="goTop">
    </div>
</template>

<script>
import CouponItem from '@/components/CouponItem.vue'
import List from '@/mixins/list'
export default {
  name: 'MyCoupon',
  components: {
    CouponItem
  },
  mixins: [List],
  data () {
    return {
      active: 'isUnused',
      list: [],
      page: 1,
      pageSize: 15,
      pageTotal: 1
    }
  },
  created () {
    this.getList()
  },
  watch: {
    active () {
      this.refresh()
      this.getList()
    }
  },
  methods: {
    async getList () {
      const { data: { list } } = await this.$api.coupon.getMyCoupon(this.active, this.page)
      const currentList = this.list
      this.list = currentList.concat(list.data)
      this.pageTotal = Math.ceil(list.total / this.pageSize)
      if (this.pageTotal === 1) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
/deep/.van-tab{
  font-size: 15px;
}
/deep/.van-tab--active{
  color: #ee0a24;
  font-weight: bold;
}
.van-tabs{
  position: fixed;
  width: 100%;
  top: 46px;
  left: 0;
  z-index: 100;
}
.list{
  height: calc(100vh - 46px);
  overflow-y: scroll;
  padding: 54px 10px 10px;
  background-color: @primaryBg;
}
.end{
  margin-top: 25px;
}
.empty{
  padding-top: 43px;
  .van-empty{
    height: calc(100vh - 200px);
  }
}
</style>
